import { postBannerStatistic } from "../../../../api/statistic";
import { platforms } from "../../../../utils/array";

export function bannerArr(banner, isClick) {
	const data = [];
	if (banner) {
		data.push({ ...banner, isClick: isClick ? true : false });
		const isMobile = navigator.userAgentData.mobile;
		const platform = platforms.find(
			item => item.platform === navigator.platform
		)?.key;
		const lang = window.localStorage.getItem("lang");
		const newdata = data.map(banner => {
			return {
				banner: banner.id,
				lang: lang,
				mobile: isMobile ? isMobile : false,
				platform: platform ? platform : 1,
				isClick: banner.isClick ? banner.isClick : false
			};
		});
		postBannerStatistic({ data: newdata });
	}
}
