import React, { useEffect, useState } from "react";
import Countdown from "react-countdown";
import { useTranslation } from "react-i18next";
import MediaImage from "../../../../components/features/product/MediaImage";

function availableSrcBGImage(backgrounds) {
	let background = Object.values(backgrounds).find(
		background => background.path
	);
	return background ? background.path : "#";
}

function IntroSlide({ item }) {
	let mount = true;
	const { t } = useTranslation();
	const [day, setDay] = useState(0);
	const [hour, setHour] = useState(0);
	const [min, setMin] = useState(0);
	const [second, setSecond] = useState(0);

	let image = availableSrcBGImage(item.config.background);
	// console.log(image);
	let time;
	function changeDates({ days, hours, minutes, seconds }) {
		time = setTimeout(() => {
			if (days !== day && mount) {
				setDay(days);
			}
			if (hours !== hour && mount) {
				setHour(hours);
			}
			if (minutes !== min && mount) {
				setMin(minutes);
			}
			if (seconds !== second && mount) {
				setSecond(seconds);
			}
		}, 1000);

		return (
			<div
				className="count-down"
				style={{
					left: "5%",
					bottom: "5%"
				}}
			>
				<div>
					<div
						style={{
							backgroundColor:
								item?.config?.countdown?.color || "#000"
						}}
					>
						{days}
						{day !== days ? <span></span> : null}
					</div>
					<p>{t("day")}</p>
				</div>
				<div>
					<div
						style={{
							backgroundColor:
								item?.config?.countdown?.color || "#000"
						}}
					>
						{hours}
						{hour !== hours ? <span></span> : null}
					</div>
					<p>{t("hour")}</p>
				</div>
				<div>
					<div
						style={{
							backgroundColor:
								item?.config?.countdown?.color || "#000"
						}}
					>
						{minutes}
						{minutes !== min ? <span></span> : null}
					</div>
					<p>{t("minute")}</p>
				</div>
				<div>
					<div
						style={{
							backgroundColor:
								item?.config?.countdown?.color || "#000"
						}}
					>
						{seconds}
						{second !== seconds ? <span></span> : null}
					</div>
					<p>{t("second")}</p>
				</div>
			</div>
		);
	}
	useEffect(() => {
		return () => {
			if (time) clearTimeout(time);
		};
	}, []);

	return (
		<div className="intro-slide">
			{item?.config?.countdown?.date ? (
				<Countdown
					date={new Date(item.config.countdown.date)}
					renderer={dates => changeDates(dates)}
				/>
			) : null}
			<a
				href={item.link && item.link.url}
				target={item.link && item.link.target ? "_blank " : "_self"}
			>
				<MediaImage url={image} alt="introslider" />
			</a>
		</div>
	);
}

export default IntroSlide;
