import React from "react";
import IntroSlide from "./IntroSlide";
import { useSelector } from "react-redux";
import Banner from "./Banner";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { sliderOptions } from "../../../../components/options";
import market from "../../../../config/market";

function Slider({ slider, size = "300x_" }) {
	let { slides } = useSelector(state => state.any);

	if (slider) slides = slider;
	if (!slides) {
		return "";
	}
	return (
		<Splide
			className="splide big-slider"
			options={
				market.config.home === 5 || market.config.home === 8
					? { ...sliderOptions, pagination: true }
					: sliderOptions
			}
		>
			{slides.map((item, index) => {
				return (
					<SplideSlide key={index}>
						{slider ? (
							<Banner
								item={item}
								size={size}
								mediumSize="800x_"
							/>
						) : (
							<IntroSlide item={item} size={size} />
						)}
					</SplideSlide>
				);
			})}
		</Splide>
	);
}

export default Slider;
