import React from "react";
import { dt } from "../../../../i18n";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import Banner from "./Banner";
import SimpleShimmer from "../../../../components/features/loaders/SimpleShimmer";
import market from "../../../../config/market";

function MiniCatSlider({ block, options }) {
	if (!block) return <SimpleShimmer height="100px" />;

	return block.dynamicCollectionBlock?.length > 0 ? (
		<Splide
			className="splide mini-slider"
			options={
				options
					? options
					: {
							pagination: false,
							perPage: 8,
							focus: "left",
							arrows: false,
							gap: "10px",
							breakpoints: {
								991: {
									perPage: 5
								},
								768: {
									perPage: 4
								},
								560: {
									perPage: 3.5
								}
							}
					  }
			}
		>
			{block.dynamicCollectionBlock.map((item, i) => {
				return (
					<SplideSlide key={i}>
						<Banner
							item={item.photos[0]}
							smallSize={"150x_"}
							size={
								market.config.home === 7 ? "original" : "800x_"
							}
						/>
						<h3 className="slider-title text-center">
							{dt(item.name)}
						</h3>
					</SplideSlide>
				);
			})}
		</Splide>
	) : null;
}

export default React.memo(MiniCatSlider);
