import React, { Fragment, useState } from "react";
import { Tab, Tabs, TabPanel, TabList } from "react-tabs";
import ProductCard from "../../../../components/features/product";
import {
	featuredSlider,
	featuredSlider4,
} from "../../../../components/options";
import { addToCart } from "../../../../actions/cart";
import ChooseVariationModal from "../../../../components/features/modal/choose-variation";
import { dt } from "../../../../i18n";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import market from "../../../../config/market";
import { toMatrix } from "../../../../utils/array";

function CollectionsWithTab({ blocks, sales, collectionRows }) {
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } },
	});
	const blocksFilter = blocks?.filter((b) => b.title === "collection") || [];
	// console.log(blocksFilter);

	const [variations, setVariations] = useState(blocksFilter[0]?.products);

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}

	if (!blocksFilter.length) {
		return "";
	}

	return (
		<div className="collectionv1">
			<Tabs selectedTabClassName="show">
				<TabList
					className={`nav nav-pills nav-border-anim nav-big justify-content-center mb-3 ${
						sales ? "d-none" : ""
					}`}
				>
					{blocksFilter.map((block, i) => {
						return (
							block.products.length > 0 && (
								<Tab
									className="nav-item"
									key={i}
									onClick={() =>
										setVariations(block.products)
									}
								>
									<span className="nav-link">
										{dt(block.name)}
									</span>
								</Tab>
							)
						);
					})}
				</TabList>
				{blocksFilter.map((item, i) => {
					return (
						item.products?.length > 0 && (
							<TabPanel key={i}>
								<Splide
									options={
										market.config.home === 9
											? featuredSlider4
											: market.config.home === 8
											? {
													...featuredSlider,
													arrows: true,
											  }
											: featuredSlider
									}
								>
									{collectionRows
										? toMatrix(
												variations,
												collectionRows
										  )?.map((row, index) => (
												<SplideSlide
													className="double-product"
													key={index}
												>
													{row &&
														row?.map(
															(
																product,
																index
															) => {
																return (
																	<Fragment
																		key={
																			index
																		}
																	>
																		<ProductCard
																			product={
																				product
																			}
																			type="sidebar"
																			onAddToCart={(
																				dataProduct
																			) => {
																				onAddToCart(
																					dataProduct
																				);
																			}}
																		/>
																	</Fragment>
																);
															}
														)}
												</SplideSlide>
										  ))
										: variations?.map((product, index) => (
												<SplideSlide
													className="double-product"
													key={index}
												>
													<ProductCard
														product={product}
														type="sidebar"
														onAddToCart={(
															dataProduct
														) => {
															onAddToCart(
																dataProduct
															);
														}}
													/>
												</SplideSlide>
										  ))}
								</Splide>
								{market.config.home === 8 ? (
									<div className="row w-100 m-auto">
										{item.products.map((product, index) => {
											return (
												<div
													className={`col-6 col-md-3 ${
														index % 2 === 0 &&
														window.innerWidth < 560
															? `pl-0`
															: ``
													}`}
													key={index}
												>
													<ProductCard
														product={product}
														type="sidebar"
														onAddToCart={(
															dataProduct
														) => {
															onAddToCart(
																dataProduct
															);
														}}
													/>
												</div>
											);
										})}
									</div>
								) : null}
							</TabPanel>
						)
					);
				})}

				<ChooseVariationModal
					setModal={setModal}
					modal={modal}
					key={1}
					addToCart={addToCart}
				/>
			</Tabs>
		</div>
	);
}

export default CollectionsWithTab;
