import React, { useState } from "react";
import { useSelector } from "react-redux";
import BannersThree from "./banners/BannersThree";
import Slider from "./banners/Slider";
// import { MenuChildList } from "./HomeMenu";
import { MenuChildListTwo } from "./MenuChildListTwo";
import HomeMenuTwo from "./HomeMenuTwo";

export default function MenuSlideSection() {
	const { menuVertical } = useSelector((state) => state.menu);
	const [childMenu, setChildMenu] = useState(null);
	const [childMenuChild, setChildMenuChild] = useState(null);

	return (
		<div
			className="menu-slider row"
			onMouseLeave={() => setChildMenu(null)}
		>
			{menuVertical.length ? (
				<div className="menus col-lg-3 only-desktop">
					<HomeMenuTwo setChildMenu={setChildMenu} />
				</div>
			) : null}
			<div
				className={`col-12 position-relative banners-childmenus 
				${menuVertical.length ? `col-lg-9` : ``}
				`}
			>
				{childMenu && childMenu.length > 0 && (
					<>
						<div className="background"></div>
						{/* <MenuChildList
							menuAllActive={[]}
							setMenuAllActive={() => {}}
							setMenuCatalog={() => {}}
							menuChild={childMenu}
							seeMore={true}
							////////////////others////////////////
						/> */}
						<MenuChildListTwo
							menuAllActive={[]}
							setMenuAllActive={() => {}}
							setMenuCatalog={() => {}}
							menuChild={childMenu}
							setChildMenuChild={setChildMenuChild}
							childMenuChild={childMenuChild}
							seeMore={true}
							///////////////////gshop///////////
						/>
					</>
				)}
				<Slider />
				<BannersThree position={`top`} />
			</div>
		</div>
	);
}
