import React from "react";
import { useSelector } from "react-redux";
import Banner from "./Banner";

function BannersThree({ block, position }) {
	const banners = useSelector(state => state.any.banners) || [];

	return (
		<div className="banners col-12 row m-0 p-0 align-items-stretch">
			{block ? (
				block[0]?.photos?.map((item, index) => {
					return <Banner item={item} key={index} />;
				})
			) : (
				<>
					<div className="first col-lg-6 col-12 row m-0 mt-1">
						{banners
							.filter(banner => banner.position === position)
							.slice(0, 2)
							.map((item, index) => {
								return (
									<div
										className="col-lg-12 col-md-6 col-12 p-0"
										key={index}
									>
										<Banner item={item} size={"800x_"} />
									</div>
								);
							})}
					</div>
					<div className="second col-lg-6 col-12 row m-0 mt-1">
						{banners
							.filter(banner => banner.position === position)
							.slice(2)
							.map((item, index) => {
								return (
									<div className="col-6 p-0" key={index}>
										<Banner item={item} size={"800x_"} />
									</div>
								);
							})}
					</div>
				</>
			)}
		</div>
	);
}

export default BannersThree;
