import React from "react";
import MediaImage from "../../../../components/features/product/MediaImage";
import { bannerArr } from "./BannerFunction";

function Banner({
	item,
	size = "original",
	smallsize,
	mediumSize,
	shimmerHeight,
}) {
	const image = item.urls ? item : item.image;
	return (
		<div className="banner-one" onClick={() => bannerArr(item, true)}>
			<a
				href={item.link}
				target={item.newTab ? "_blank" : "_self"}
				rel="noreferrer"
			>
				<MediaImage
					image={image}
					largeSize={size}
					mediumSize={mediumSize ? mediumSize : "800x_"}
					smallSize={smallsize ? smallsize : "800x_"}
					alt={item.name}
					shimmerHeight={shimmerHeight}
				/>
			</a>
		</div>
	);
}

export default Banner;
