import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { setCategoryPath } from "../../actions/any";
import market from "../../config/market";
import HomePageEleven from "./home-eleven";

const HomePageDefault = React.lazy(() => import("./home-one"));
const HomePageTwo = React.lazy(() => import("./home-two"));
const HomePageThree = React.lazy(() => import("./home-three"));
const HomePageFour = React.lazy(() => import("./home-four"));
const HomePageFive = React.lazy(() => import("./home-five"));
const HomePageSix = React.lazy(() => import("./home-six"));
const HomePageSeven = React.lazy(() => import("./home-seven"));
const HomePageEight = React.lazy(() => import("./home-eight"));
const HomePageNine = React.lazy(() => import("./home-nine"));
const HomePageTen = React.lazy(() => import("./home-ten"));

export default function HomePage() {
	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(setCategoryPath(null));
	}, []);

	switch (market.config.home) {
		case 1:
			return <HomePageDefault key="1" />;
		case 2:
			return <HomePageTwo key="2" />;
		case 3:
			return <HomePageThree key="3" />;
		case 4:
			return <HomePageFour key="4" />;
		case 5:
			return <HomePageFive key="5" />;
		case 6:
			return <HomePageSix key="6" />;
		case 7:
			return <HomePageSeven key="7" />;
		case 8:
			return <HomePageEight key="8" />;
		case 9:
			return <HomePageNine key="9" />;
		case 10:
			return <HomePageTen key="10" />;
		case 11:
			return <HomePageEleven key="11" />;
		default:
			return <HomePageDefault key="1" />;
	}
}
