import React, { Fragment, useEffect, useState } from "react";
import ProductCard from "../../../../components/features/product";
import { addToCart } from "../../../../actions/cart";
import ChooseVariationModal from "../../../../components/features/modal/choose-variation";
import { useTranslation } from "react-i18next";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { collectionFour, featuredSlider } from "../../../../components/options";
import ShimmerProducts from "../../../../components/features/loaders/ShimmerProducts";
import market from "../../../../config/market";

function LatestCollectionThree({ collection, title, width }) {
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } },
	});
	const [double, setDouble] = useState([]);
	const { t } = useTranslation();

	useEffect(() => {
		if (collection && collection.products) {
			const toMatrix = (arr, width = 2) =>
				arr.reduce(
					(rows, key, index) =>
						(index % width === 0
							? rows.push([key])
							: rows[rows.length - 1].push(key)) && rows,
					[]
				);
			setDouble(toMatrix(collection.products, width));
		}
	}, [collection]);

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}

	if (!collection) {
		return <ShimmerProducts column={10} />;
	}

	return (
		<div className="collections-three">
			<h2 className="title">{t(title)}</h2>
			<ul className="only-desktop">
				{collection?.products?.map((product, index) => (
					<li key={index} className="product-item">
						<ProductCard
							product={product}
							type="sidebar"
							onAddToCart={(dataProduct) => {
								onAddToCart(dataProduct);
							}}
						/>
					</li>
				))}
			</ul>
			<div className="only-mobile">
				<Splide
					className="splide"
					options={
						market.config.home === 8
							? {
									...featuredSlider,
									arrows: true,
									breakpoints: {
										576: {
											perPage: 2,
											gap: "10px",
										},
										768: {
											perPage: 2,
											gap: "10px",
										},
										1024: {
											perPage: 5,
										},
									},
							  }
							: collectionFour
					}
				>
					{double.map((row, index) => (
						<SplideSlide className="double-product" key={index}>
							{row &&
								row.map((product, index) => {
									return (
										<Fragment key={index}>
											<ProductCard
												product={product}
												type="sidebar"
												onAddToCart={(dataProduct) => {
													onAddToCart(dataProduct);
												}}
											/>
										</Fragment>
									);
								})}
						</SplideSlide>
					))}
				</Splide>
			</div>

			<ChooseVariationModal
				setModal={setModal}
				modal={modal}
				addToCart={addToCart}
			/>
		</div>
	);
}

export default LatestCollectionThree;
